/* src/App.css - mobile refined + chart x-label visible */

:root{
  --bg:#f6fbfc;
  --card:#ffffff;
  --muted:#9aa6b2;
  --accent-start:#2bd0c8;
  --accent-end:#1f6ef8;
  --pill:#e9f6ff;
}

*{box-sizing:border-box;font-family:'Helvetica Neue',Arial,sans-serif}
body,html,#root{height:100%;margin:0;background:var(--bg);color:#0b1a23}

.app{max-width:920px;margin:0 auto;padding:16px 10px 80px}
.app-header{display:flex;flex-wrap:wrap;align-items:center;justify-content:space-between;gap:8px}
.title{font-size:36px;margin:0;font-weight:800;line-height:1.1;white-space:nowrap}
.tab-buttons{display:flex;gap:6px}
.tab{padding:6px 12px;border-radius:16px;border:none;background:transparent;white-space:nowrap}
.tab.active{background:linear-gradient(90deg,var(--accent-start),var(--accent-end));color:#fff}

.main{margin-top:8px}
.top-row{display:flex;flex-wrap:wrap;gap:10px;align-items:center}
.avg-card{padding:12px;border-radius:12px;background:#fff;box-shadow:0 6px 16px rgba(0,0,0,.06)}
.avg-number{font-size:28px;font-weight:800}

.filter-row{display:flex;gap:6px;flex-wrap:wrap}
.filter-select,.reset-ui-btn{padding:6px 8px;border-radius:10px;border:1px solid #e6eef5;background:#fff}

.controls{margin-top:10px;display:flex;flex-direction:column;gap:6px}

.date-row input{padding:6px 8px;border-radius:8px;border:1px solid #e6eef5}

.hand-cards{display:flex;gap:8px;overflow-x:auto;margin-top:8px}
.hand-card{flex:0 0 110px;padding:10px;border-radius:12px;background:#fff;text-align:center}
.hand-card.selected{outline:2px solid #2ea8ff}
.hand-emoji{font-size:30px}

.result-row{display:flex;gap:6px;margin-top:8px}
.pill{padding:6px 10px;border-radius:14px;background:var(--pill)}
.pill.active{background:linear-gradient(90deg,var(--accent-start),var(--accent-end));color:#fff}

.save-btn{margin-top:8px;padding:10px;border-radius:14px;background:linear-gradient(90deg,var(--accent-start),var(--accent-end));color:#fff}

.history-table{width:100%;margin-top:12px;border-collapse:collapse;font-size:13px}
.history-table th,.history-table td{padding:8px;white-space:nowrap}

.stats-section{margin-top:16px}
.chart-wrap{height:220px;overflow-x:auto;overflow-y:hidden}
.chart-inner{min-width:520px;height:220px;padding-bottom:40px}
